<template>
 <div>
   <el-form ref="form" :model="form" label-width="80px" label-position="left">
     <el-form-item label="">
       <el-input v-model="form.name"></el-input>
     </el-form-item>
     <el-form-item label="">
       <el-select  v-model="form.region" placeholder="" style="width: 100%">
         <el-option label="" value="shanghai"></el-option>
         <el-option label="" value="beijing"></el-option>
       </el-select>
     </el-form-item>
     <el-form-item label="">
       <el-col :span="11">
         <el-date-picker type="date" placeholder="" v-model="form.date1" style="width: 100%;"></el-date-picker>
       </el-col>
       <el-col class="line" :span="2">-</el-col>
       <el-col :span="11">
         <el-time-picker placeholder="" v-model="form.date2" style="width: 100%;"></el-time-picker>
       </el-col>
     </el-form-item>
     <el-form-item label="">
       <el-switch v-model="form.delivery" style="width: 100%"></el-switch>
     </el-form-item>
     <el-form-item label="">
       <el-checkbox-group v-model="form.type" style="width: 100%">
