       </el-radio-group>
     </el-form-item>
     <el-form-item label="">
       <el-input type="textarea" v-model="form.desc"></el-input>
     </el-form-item>
     <el-form-item>
       <el-button type="primary" @click="onSubmit"></el-button>
       <el-button></el-button>
     </el-form-item>
   </el-form>
 </div>
</template>

<script>
    export default {
      name: 'Form',
      data () {
        return {
          form: {
            name: '',
            region: '',
            date1: '',
            date2: '',
            delivery: false,
            type: [],
            resource: '',
            desc: ''
          }
        }
      },
      methods: {
        onSubmit: function () {
          console.log('summit')
        }
      }
    }
</script>

<style scoped>
  .el-form {
    width: 600px;
  }
</style>



